.abc__letter {
  border: 1px solid #d7dbdd;
  border-radius: 50%;
  color: #676d72;
  display: inline-block;
  font-style: normal;
  height: 20px;
  line-height: 20px;
  margin: -2px auto;
  text-align: center;
  vertical-align: middle;
  width: 20px; }
  .abc__letter:hover {
    background-color: #b01e21;
    border-color: #b01e21;
    color: #fff; }

.abc {
  margin-bottom: 30px; }
  .abc__letter {
    background-color: #7aa104;
    border-color: #7aa104;
    color: #fff;
    margin-bottom: 0.71429em; }
  .abc__link {
    color: #222222;
    text-decoration: none; }

@media (max-width: 767px) {
  .abc_row > .abc:nth-child(2n + 1) {
    clear: both; } }

@media (min-width: 768px) and (max-width: 991px) {
  .abc_row > .abc:nth-child(3n + 1) {
    clear: both; } }

@media (min-width: 992px) {
  .abc_row > .abc:nth-child(4n + 1) {
    clear: both; } }
